body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.Blockquote {
    background-color: #ffffff;
    border-radius: 0.25rem;
    box-shadow: 0px 1px 5px 0px rgba(0,0,0,0.2), 0px 2px 2px 0px rgba(0,0,0,0.14), 0px 3px 1px -2px rgba(0,0,0,0.12);
    max-width: 75vw;
    padding: 2rem 2.5rem;
}

.Blockquote__quotation {
    border-left: 0.25rem solid #e0e0e0;
    color: #5a5a5a;
    font-style: italic;
    line-height: 1.3;
    margin: 0;
    padding: 0.5rem 0 0.5rem 1rem;
    white-space: pre-line;
}

.Blockquote__author {
    display: block;
    font-size: 0.75rem;
    margin-top: 1rem;
    text-align: right;
}
.Button {
    align-items: center;
    border-color: transparent;
    border-style: solid;
    border-width: 1px;
    border-radius: 0.25rem;
    box-sizing: border-box;
    cursor: pointer;
    display: inline-flex;
    font-family: "Roboto", "Helvetica", "Arial", sans-serif;
    font-size: 0.875rem;
    font-weight: 500;
    justify-content: center;
    min-height: 2rem;
    min-width: 4rem;
    outline: 0;
    position: relative;
    text-decoration: none;
    text-transform: uppercase;
    -webkit-transition: background-color 250ms cubic-bezier(0.4, 0, 0.2, 1) 0ms,box-shadow 250ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;
    transition: background-color 250ms cubic-bezier(0.4, 0, 0.2, 1) 0ms,box-shadow 250ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    vertical-align: middle;
    -webkit-appearance: none;
    -webkit-tap-highlight-color: transparent;
}

.Button__label {
    align-items: inherit;
    display: inherit;
    justify-content: inherit;
    width: 100%;
}

/* TEXT BUTTON STYLES */
.Button--primary,
.Button--grey {
    background-color: transparent;
    padding: 0.5rem;
}

/* CONTAINED BUTTON STYLES */
.Button--primary--contained,
.Button--grey--contained {
    box-shadow: 0px 1px 5px 0px rgba(0,0,0,0.2), 0px 2px 2px 0px rgba(0,0,0,0.14), 0px 3px 1px -2px rgba(0,0,0,0.12);
    padding: 0.5rem 1rem;
}

.Button--primary--contained:active,
.Button--grey--contained:active {
    box-shadow: 0px 5px 5px -3px rgba(0,0,0,0.2), 0px 8px 10px 1px rgba(0,0,0,0.14), 0px 3px 14px 2px rgba(0,0,0,0.12);
}

/* PRIMARY TEXT BUTTON STYLES */
.Button--primary {
    color: #3f51b5;
}

.Button--primary:focus,
.Button--primary:hover {
    background-color: #f0f1f9;
}

/* PRIMARY CONTAINED BUTTON STYLES */
.Button--primary--contained {
    background-color: #3f51b5;
    border-color: transparent;
    color: #ffffff;
}

.Button--contained.Button--primary:focus,
.Button--contained.Button--primary:hover {
    background-color: #303f9f;
}

/* GREY TEXT BUTTON STYLES */
.Button--grey {
    color: #313131;
}

.Button--grey:focus,
.Button--grey:hover {
    background-color: #ebebeb;
}

/* GREY CONTAINED BUTTON STYLES */
.Button--grey--contained {
    background-color: #e0e0e0;
    color: #313131;
}

.Button--grey--contained:focus,
.Button--grey--contained:hover {
    background-color: #d5d5d5;
}
.Tag {
    position: relative;
}

.Tag__button {
    align-items: center;
    border-style: solid;
    border-radius: 1rem;
    border-width: 1px;
    box-sizing: border-box;
    cursor: pointer;
    display: inline-flex;
    font-family: "Roboto", "Helvetica", "Arial", sans-serif;
    font-size: 0.8125rem;
    height: 2rem;
    justify-content: center;
    position: relative;
    text-decoration: none;
    -webkit-transition: background-color 300ms cubic-bezier(0.4, 0, 0.2, 1) 0ms, box-shadow 300ms cubic-bezier(0.4, 0, 0.2, 1) 0ms, border-color 300ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;
    transition: background-color 300ms cubic-bezier(0.4, 0, 0.2, 1) 0ms, box-shadow 300ms cubic-bezier(0.4, 0, 0.2, 1) 0ms, border-color 300ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;
    vertical-align: middle;
    white-space: nowrap;
}

input[type="checkbox"].Tag__checkbox {
    height: 100%;
    margin: 0;
    opacity: 0;
    position: absolute;
    width: 100%;
}

.Tag__text {
    cursor: inherit;
    padding: 0 0.75rem;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
}

/* GREY TAG STYLES */
.Tag--grey input[type="checkbox"].Tag__checkbox:focus + .Tag__button,
.Tag--grey input[type="checkbox"].Tag__checkbox:hover + .Tag__button,
.Tag--grey--outline input[type="checkbox"].Tag__checkbox:focus + .Tag__button,
.Tag--grey--outline input[type="checkbox"].Tag__checkbox:hover + .Tag__button {
    border-color: #9a9a9a;
}

/* GREY CONTAINED TAG STYLES */
.Tag--grey .Tag__button {
    background-color: #e0e0e0;
    border-color: #e0e0e0;
}

.Tag--grey .Tag__button:active {
    background-color: #c4c4c4;
    box-shadow: 0px 1px 3px 0px rgba(0,0,0,0.2), 0px 1px 1px 0px rgba(0,0,0,0.14), 0px 2px 1px -1px rgba(0,0,0,0.12);
}

.Tag--grey input[type="checkbox"].Tag__checkbox:checked + .Tag__button {
    background-color: #cecece;
}

/* GREY OUTLINE TAG STYLES */
.Tag--grey--outline .Tag__button {
    background-color: transparent;
    border-color: #e0e0e0;
    color: #313131;
}

.Tag--grey--outline .Tag__button:active {
    background-color: #c5c5c5;
    box-shadow: 0px 1px 3px 0px rgba(0,0,0,0.2), 0px 1px 1px 0px rgba(0,0,0,0.14), 0px 2px 1px -1px rgba(0,0,0,0.12);
}

.Tag--grey--outline input[type="checkbox"].Tag__checkbox:checked + .Tag__button {
    background-color: #ebebeb;
}
.Form__inline-input-container {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    padding: 1rem;
    width: 75vw;
}

.Form__inline-input-container__input {
    margin: 0.25rem;
}

.color--grey {
  color: #5a5a5a;
}

.container {
  align-items: center;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  justify-content: center;
  min-height: 100vh;
  padding: 1rem;
}

.font-size--sm {
  font-size: 0.75rem;
}

.icon--lg {
  line-height: 0.8;
  font-size: 1.5rem;
}

.margin--xs {
  margin: 0.25rem;
}

.margin-bottom--sm {
  margin-bottom: 0.5rem;
}

.margin-top--md {
  margin-top: 1rem;
}

.margin-top--sm {
  margin-top: 0.5rem;
}

.margin-right--xs {
  margin-right: 0.25rem;
}

